.card-container {
  width: 100%;
}

.custom-card {
  width: 100%;
  height: 0; /* Hauteur 0 pour conserver le ratio */
  padding-bottom: 100%; /* Crée un carré en utilisant le padding */
  position: relative;
  overflow: hidden;
}

.card-img-top {
  width: 300px;
  height: 300px;
  object-fit: cover;
  transition: filter 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.custom-card:hover .card-img-top {
  filter: grayscale(100%) blur(5px);
  transform: scale(1.1); /* Légèrement agrandi au survol */
}

.card-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: black;
  font-weight: bold;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.custom-card:hover .card-text {
  opacity: 1;
}
